html {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 14px;
  box-sizing: border-box;
  color: #262626;
  line-height: 1.3;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*:-webkit-full-screen-ancestor {
  animation-name: none !important;
  will-change: initial;
}

body.loading {
  cursor: wait;
}

*, *:before, *:after {
  box-sizing: inherit;
}

p {
  margin: 0 0 1rem 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 1rem 0;
}

h1 {
  font-size: 18px;
  color: #262626;
}

h2 {
  font-size: 17px;
}

h3 {
  font-size: 15px;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #258db8;
  transition: color 0.2s ease;
  font-weight: 600;
}

a.thin {
  font-weight: normal;
}

a:hover {
  color: #39a9d7;
}

a:active {
  color: #144c63;
}

.badge {
  display: inline-block;
  font-size: 12px;
  background: #e6007e;
  color: #fff;
  line-height: 1.3;
  padding: 0 5px;
  border-radius: 3px;
  font-weight: bold;
}

.color-gray {
  color: #999;
}

.color-warning {
  color: #d84c3d;
}

.color-primary {
  color: #258db8;
}

.color-base {
  color: #262626 !important;
}

small,
.small-font {
  font-size: 12px;
  line-height: 1.5;
}

.fade-out {
  color: #999 !important;
}

.fade-out a {
  color: #999 !important;
  text-decoration: underline;
  font-weight: normal;
}

.fade-out a:hover {
  color: #666666 !important;
}

.unimportant {
  color: #ccc !important;
}

.unimportant a {
  color: #ccc !important;
  text-decoration: underline;
  font-weight: normal;
}

.unimportant a:hover {
  color: #999999 !important;
}

.inherit-link, .inherit-color a {
  color: #262626;
  text-decoration: underline;
  font-weight: normal;
}

.inherit-link:hover, .inherit-color a:hover, .breadcrumb a:hover {
  color: #999;
}

.inherit-link.plain, .inherit-color a.plain {
  text-decoration: none;
}

.text-centered {
  text-align: center;
}

.text-right {
  text-align: right;
}

ol, ul {
  padding-left: 1.3rem;
  margin-top: 0;
}

ol.plain, ul.plain {
  padding: 0;
  list-style: none;
}

ol.spacey li:not(:last-of-type), ul.spacey li:not(:last-of-type) {
  margin-bottom: .5rem;
}

ol.inline li, ul.inline li {
  display: inline;
}

ol.inline li:not(:last-of-type):after, ul.inline li:not(:last-of-type):after {
  content: ', ';
}

ol.inline.separate li:not(:last-of-type):after, ul.inline.separate li:not(:last-of-type):after {
  content: ' | ';
}

ol.inline.custom li:not(:last-of-type):after, ul.inline.custom li:not(:last-of-type):after {
  content: attr(data-separator);
}

ol.inline.spacey li, ul.inline.spacey li {
  display: inline-block;
}

ol.inline.spacey li:not(:last-of-type), ul.inline.spacey li:not(:last-of-type) {
  margin-right: 1.5rem;
  margin-bottom: 0;
}

ol.inline.spacey li:not(:last-of-type):after, ul.inline.spacey li:not(:last-of-type):after {
  content: '';
}

ol.space li:not(:first-of-type), ul.space li:not(:first-of-type) {
  margin-top: 1rem;
}

.highlight-span span {
  color: #258db8;
  font-weight: bold;
}

.unbreakable {
  white-space: nowrap;
}

.no-margin {
  margin: 0 !important;
}

.tight {
  margin-bottom: 0 !important;
}

.half-space {
  margin-bottom: .5rem !important;
}

.single-space {
  margin-bottom: 1rem !important;
}

.double-space {
  margin-bottom: 2rem !important;
}

.weight-normal {
  font-weight: normal !important;
}

kbd {
  background: #f1f1f1;
  padding: 2px;
  display: inline-block;
  border-radius: 3px;
}

::selection {
  background: #79c5e4;
  color: #fff;
}

.cursor-help {
  cursor: help !important;
}

.cursor-pointer {
  cursor: pointer !important;
}

@media all and (min-width: 768px) {
  h1 {
    font-size: 20px;
  }
  ol.custom-desktop li, ul.custom-desktop li {
    display: inline-block;
  }
  ol.custom-desktop.spacey li:not(:last-of-type), ul.custom-desktop.spacey li:not(:last-of-type) {
    margin-bottom: 0rem;
  }
  ol.custom-desktop li:not(:last-of-type):after, ul.custom-desktop li:not(:last-of-type):after {
    content: attr(data-separator);
  }
}

@media all and (max-width: 767px) {
  ol.inline.spacey li, ul.inline.spacey li {
    display: block;
  }
  ol.inline.spacey li:after, ul.inline.spacey li:after {
    content: '';
  }
  ol.inline.spacey li:not(:last-of-type), ul.inline.spacey li:not(:last-of-type) {
    margin-bottom: .5rem;
    margin-right: 0;
  }
}

/**
* Code formatting for jekyll
*/
code {
  background: #f2f2f2;
  padding: 3px;
  word-break: break-all;
  word-break: break-word;
}

.code pre,
pre > code {
  display: block;
  font-family: monospace;
  padding: 20px;
  border-radius: 3px;
  border-bottom: 2px solid #cccccc;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  word-break: normal;
}

.linenos code {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-right: 1px solid #cccccc;
  padding: 20px 10px;
  text-align: center;
  color: #999;
}

.linenos + .code pre {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.highlighttable {
  width: 100%;
  display: flex;
}

.highlighttable tbody {
  width: 100%;
  display: block;
}

.highlighttable tr {
  display: flex;
}

.highlighttable .code {
  min-width: 0;
  display: flex;
  flex: 1;
  width: 100%;
}

.highlighttable .highlight {
  min-width: 0;
  width: 100%;
}

/**
* Basic Layout
*/
.wrapper {
  display: flex;
  flex: 1 0 auto;
  flex-flow: row wrap;
  width: 100%;
  min-height: calc(100vh - 43px);
  margin-top: 43px;
}

.wrapper > * {
  flex: 1 100%;
}

.wrapper-center {
  max-width: 1200px;
  margin: 43px auto 0 auto;
}

html.modal-open body {
  overflow: hidden;
}

.content-wrapper {
  display: flex;
  flex-direction: column;
}

.page-content {
  padding: 25px 10px;
}

.page-content .page-content {
  padding: 0 10px;
}

.header {
  height: 43px;
  background: #333333;
  font-size: 18px;
  position: fixed;
  z-index: 4;
  top: 0;
  width: 100%;
  backface-visibility: hidden;
}

.topbar {
  padding: 0 10px 0 5px;
  color: #aaa;
  line-height: 43px;
}

.navigation-toggler:before {
  margin-right: 10px;
  line-height: 43px;
}

.navigation-toggler span {
  font-size: 14px;
  vertical-align: 1px;
}

.footer {
  min-height: 100px;
  text-align: center;
}

.main {
  text-align: left;
}

.sidebar {
  will-change: transform;
  padding: 20px;
  background: #fafafa;
  position: fixed;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  width: 265px;
  z-index: 22;
  transform: translate3d(100%, 0, 0);
  transition: transform 0.35s ease;
  top: 0;
  right: 0px;
}

.sidebar.open {
  transform: translate3d(0%, 0, 0);
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 0;
}

.darkener {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  will-change: opacity;
  opacity: 0.35;
  background: #000;
  z-index: 21;
  display: none;
  cursor: pointer;
}

.darkener:not(.init) {
  animation-name: darkenerFadeOut;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
}

.toggled-canvas .darkener {
  display: block;
}

.toggled-canvas .darkener.init {
  animation-name: darkenerFadeIn;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
}

.flex-align-center {
  align-self: center;
}

/* Main Navigation Container, Off-Screen Navigation*/
.navbar-main {
  position: fixed;
  height: 100%;
  overflow-y: auto;
  font-size: 14px;
  -webkit-overflow-scrolling: touch;
  width: 250px;
  background: #4a4a4a;
  z-index: 22;
  will-change: transform;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.35s ease;
  top: 0;
}

.navbar-main.open {
  transform: translate3d(0%, 0, 0);
}

.navbar-main a:not(.button) {
  color: #aaa;
}

.navbar-main a:not(.button):active:not(.logo) {
  background: rgba(0, 0, 0, 0.3) !important;
}

.navbar-main a:not(.button):hover {
  color: #fff;
}

.navbar-main ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navbar-main .more-space {
  margin-top: 1rem;
}

.navbar-main ul {
  display: flex;
  flex-direction: column;
}

.navbar-main ul li.active a {
  background: rgba(0, 0, 0, 0.2);
  font-weight: bold;
  color: #fff;
}

.navbar-main ul li a:not(.button) {
  padding: 10px 20px;
  display: inline-block;
  width: 100%;
  position: relative;
}

.navbar-main ul li a:not(.button) .badge {
  position: absolute;
  right: 20px;
  margin-top: 2px;
}

.home {
  display: block;
  text-indent: -99999px;
  background: #333333;
  height: 43px;
}

.topbar {
  display: flex;
}

.topbar-action {
  cursor: pointer;
  display: inline-block;
  padding: 0 3px;
  font-weight: bold;
  color: #aaa;
  min-width: 30px;
  text-align: center;
}

.topbar-action > * {
  pointer-events: none;
}

.topbar-action.active, .topbar-action:active {
  background: rgba(0, 0, 0, 0.2);
}

.topbar-action.right {
  margin-left: 15px;
}

.topbar-action:hover {
  color: #fff;
}

.topbar-action .badge {
  vertical-align: 1px;
}

.topbar-action span:not(.badge) {
  font-size: 14px;
  vertical-align: top;
}

.navigation-toggler {
  flex: 1;
  text-align: left;
  white-space: nowrap;
  padding-right: 30px;
}

.navigation-toggler span {
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
  overflow: hidden;
  margin: 0;
  color: #fff;
}

.navigation-toggler span.logo {
  width: 100px;
}

.subnav-content {
  overflow: hidden;
  position: relative;
  transform: translateZ(0px);
}

.subnav-content:after {
  content: ' ';
  background: linear-gradient(to right, rgba(241, 241, 241, 0) 0%, #f1f1f1 100%);
  width: 60px;
  height: 43px;
  position: absolute;
  right: 0;
  top: 0;
  pointer-events: none;
}

/**
* A general overflow container to visual
*/
.overflow-container {
  position: relative;
  transform: translateZ(0px);
}

.overflow-container:after {
  content: ' ';
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fff 100%);
  width: 60px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  pointer-events: none;
}

/**
* Subnavigation and tabs
*/
.subnav, .tabs {
  background: #f1f1f1;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  margin: 0;
  padding: 0;
  list-style: none;
  white-space: nowrap;
  transform: translateZ(0px);
}

.subnav li, .tabs li {
  display: inline-block;
  margin-right: 0px;
}

.subnav li a, .subnav li .tab-item, .tabs li a, .tabs li .tab-item {
  padding: 0 10px;
  color: #616161;
  font-weight: normal;
  line-height: 43px;
  display: block;
}

.subnav li a:not(span):hover, .subnav li .tab-item:not(span):hover, .tabs li a:not(span):hover, .tabs li .tab-item:not(span):hover {
  color: #949494;
}

.subnav li.active a, .tabs li.active a {
  font-weight: bold;
  color: #616161;
}

.subnav li:last-of-type:not(.trigger), .tabs li:last-of-type:not(.trigger) {
  margin-right: 40px;
}

.subnav input, .tabs input {
  background: transparent !important;
}

.tabs {
  background: transparent;
  position: relative;
  border-bottom: 1px solid #f1f1f1;
  z-index: 0;
}

.tabs li {
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  position: relative;
}

.tabs li a, .tabs li .tab-item {
  line-height: 40px;
}

.tabs li.loading a {
  cursor: wait;
}

.tabs li.loading:after {
  animation-name: tabWobbleLine;
  animation-duration: 0.4s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

