/*
* A Modal Component
*/
.modal-container {
  /* autoprefixer: off */
  perspective: 1000px;
  backface-visibility: hidden;
}

/* Container that contains all Modals of an instance */
.modal-container {
  user-select: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background: rgba(0, 0, 0, 0.35);
  animation-name: backgroundFadeIn;
  animation-duration: 0.25s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
  z-index: 22;
  display: none;
  -webkit-overflow-scrolling: touch;
  min-height: 100%;
  overflow-y: scroll;
}

.modal-container.front {
  z-index: 23;
}

.modal-container.open {
  display: block;
}

.modal-container > .loader-container {
  align-self: center;
  position: fixed;
  left: calc(50% - 20px);
  top: calc(50% - 20px);
  z-index: 101;
  animation-name: fadeIn;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
}

.modal-container.loading {
  cursor: wait;
}

.modal-open body {
  width: 100%;
}

.modal {
  user-select: text;
  width: 100%;
  position: relative;
  background: #fff;
  display: none;
  padding: 30px 12px 25px;
  align-self: flex-start;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.7);
}

.modal.current {
  display: block;
  animation-name: bounceIn;
  animation-duration: 0.6s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: linear;
}

.modal.current ~ .loader-container {
  left: calc(50% - 30px);
  top: calc(50% - 30px);
  background: #fff;
  padding: 10px;
  border-radius: 3px;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.7);
}

.modal.modal-anim-end {
  animation-name: none !important;
}

.modal.part-of-stack {
  display: block;
  position: absolute;
  pointer-events: none;
  animation-name: fadeOut;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-direction: normal;
}

.modal.part-of-stack.modal-anim-end {
  display: none;
}

.modal-container .modal.current {
  z-index: 10;
}

.modal-close {
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 3;
  padding: 9px 10px 10px;
  font-size: 16px;
  cursor: pointer;
  color: #a6a6a6;
