/*
* A simple slider
*/
@-webkit-keyframes slider {
  0%, 40% {
    transform: translate3d(0, 0, 0);
  }
  50%, 100% {
    transform: translate3d(-100%, 0, 0);
  }
}

@-moz-keyframes slider {
  0%, 40% {
    transform: translate3d(0, 0, 0);
  }
  50%, 100% {
    transform: translate3d(-100%, 0, 0);
  }
}

@-ms-keyframes slider {
  0%, 40% {
    transform: translate3d(0, 0, 0);
  }
  50%, 100% {
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes slider {
  0%, 40% {
    transform: translate3d(0, 0, 0);
  }
  50%, 100% {
    transform: translate3d(-100%, 0, 0);
  }
}

.slide {
  min-width: 100%;
  display: inline-block;
  position: relative;
  height: 100%;
  flex: 1;
  overflow: hidden;
}

.slider {
  list-style: none;
