@media all and (min-width: 900px) {
  .labeled:not(.wide).three-columns > dd {
    padding-right: calc(25% + .375rem);
  }
  .labeled:not(.wide).between-thirty-sixty > dt {
    width: 35%;
  }
  .labeled:not(.wide).between-thirty-sixty > dd {
    width: 65%;
  }
  .labeled:not(.wide).between-fifty-fifty > dd,
  .labeled:not(.wide).between-fifty-fifty > dt {
    width: 50%;
  }
}

@media all and (min-width: 768px) {
  .labeled:not(.wide).desktop-fifty-fifty > dd,
  .labeled:not(.wide).desktop-fifty-fifty > dt {
    width: 50%;
  }
}

/* Everything about tables */
table {
  width: 100%;
  border-spacing: 0;
  margin: 0rem 0 1rem 0;
}

table thead tr, table tbody tr {
  border-top: 1px solid #f1f1f1;
  transition: background 0.1s ease;
}

table tbody tr:last-of-type {
  border-bottom: 1px solid #f1f1f1;
}

table th {
  text-align: left;
  color: #258db8;
  font-weight: normal;
}

table th, table td {
  padding: 10px 1rem 10px 0;
}

table th:not(:first-of-type), table td:not(:first-of-type) {
  padding-left: 5px;
}

table th:last-of-type, table td:last-of-type {
  padding-right: 5px;
}

table th:first-of-type, table td:first-of-type {
  padding-left: 5px;
}

table:not(.no-hover) tbody > tr:hover {
  background: #f8f8f8;
}

table th.one, table td.one {
  width: 8.33333%;
}

table th.two, table td.two {
  width: 16.66667%;
}

table th.three, table td.three {
  width: 25%;
}

table th.four, table td.four {
  width: 33.33333%;
}

table th.five, table td.five {
  width: 41.66667%;
}

table th.six, table td.six {
  width: 50%;
}

table th.seven, table td.seven {
  width: 58.33333%;
}

table th.eight, table td.eight {
  width: 66.66667%;
}

table th.nine, table td.nine {
  width: 75%;
}

table th.ten, table td.ten {
  width: 83.33333%;
}

table th.eleven, table td.eleven {
  width: 91.66667%;
}

@media all and (max-width: 767px) {
  table th.small-one, table td.small-one {
    width: 8.33333%;
  }
  table th.small-two, table td.small-two {
    width: 16.66667%;
  }
  table th.small-three, table td.small-three {
    width: 25%;
  }
  table th.small-four, table td.small-four {
    width: 33.33333%;
  }
  table th.small-five, table td.small-five {
    width: 41.66667%;
  }
  table th.small-six, table td.small-six {
    width: 50%;
  }
  table th.small-seven, table td.small-seven {
    width: 58.33333%;
  }
  table th.small-eight, table td.small-eight {
    width: 66.66667%;
  }
  table th.small-nine, table td.small-nine {
    width: 75%;
  }
  table th.small-ten, table td.small-ten {
    width: 83.33333%;
  }
  table th.small-eleven, table td.small-eleven {
    width: 91.66667%;
  }
}

table.nowrap th, table.nowrap td {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 10px;
}

table.plain tr {
  border: none !important;
}

table.fixed {
  table-layout: fixed;
}

table.align-top td {
  vertical-align: top;
}

@media all and (max-width: 767px) {
  table.break tr, table.break td, table.break th {
    display: block;
    width: 100%;
  }
  table.break th, table.break td {
    padding: 6px 1rem 6px 0;
  }
}

/* All stuff about listings */
.listing {
  position: relative;
  display: flex;
  width: 100%;
  padding: 10px 0;
  border-bottom: 1px solid #f1f1f1;
  transition: background 0.2s ease;
  will-change: background;
  margin: 0;
}

.listing:hover:not(.editor) {
  background: #f8f8f8;
}

.listing h4 {
  margin: 0;
}

.listing .color-warning {
  margin: 0 0 .5rem 0;
}

.listing.highlight:before {
  content: ' ';
  position: absolute;
  width: 2px;
  background: #258db8;
  left: 0;
  height: 100%;
  top: 0;
  pointer-events: none;
}

.listing.centered {
  align-items: center;
}

.listing .right-absolute {
  padding-right: 10px;
}

.listing:first-of-type {
  border-top: 1px solid #f1f1f1;
  margin-top: 0;
}

.listing p {
  margin: 0;
}

.listing .right-absolute {
  position: absolute;
  right: 0;
  padding: 0 0 0 10px;
  top: -10px;
  background: #fff;
}

.listing .title {
  flex: 1;
  padding: 0 20px 0 10px;
  min-width: 0;
}

.listing .avatar-row + .title {
  padding-left: 0;
}

.listing .meta-bar {
  font-size: 12px;
  color: #999;
  padding: 0 12px;
}

.listing .meta-bar .stats > * {
  min-width: 40px;
  display: inline-block;
}

.listing .actions {
  transition: opacity 0.2s ease;
  padding: 1.75rem 0 5px 0;
  display: flex;
  align-items: center;
}

.listing .actions > * {
  flex: 1;
}

.listing .actions .right {
  text-align: right;
}

.listing .actions .right .dropdown {
  text-align: left;
}

.clickable {
  cursor: pointer;
}

.clickable:active {
  background: #eeeeee;
}

.control-row {
  align-self: center;
  padding: 0 10px 0 0;
}

.equal-row {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.equal-row > * {
  flex: 1;
  min-width: 0;
}

.equal-row > * > * {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.content-row {
  flex: 1;
  min-width: 0;
}

.avatar-row {
  min-width: 84px;
}

.avatar-row .stats {
  text-align: center;
  margin-top: 5px;
}

.avatar-row .avatar {
  margin: 0 auto;
}

.avatar-row + .content-row {
  padding-left: 0;
}

.avatar-row-small {
  min-width: 36px !important;
  max-width: 36px !important;
  margin-right: 13px !important;
  margin-left: 13px !important;
}

.avatar-row-small .avatar {
  width: 100%;
}

html:not(.touch) .listing:not(:hover) .actions {
  opacity: 0;
}

html:not(.touch) .listing:not(:hover) .control-row .button, html:not(.touch) .listing:not(:hover) .transparent-actions .button {
  background: transparent;
  color: #999;
}

html:not(.touch) .fade-listing:not(:hover) .fade-out {
  color: #d9d9d9;
}

@media all and (min-width: 768px) {
  .control-row {
    padding: 0 20px;
  }
  .meta-bar .stats > * {
    min-width: 50px;
  }
  .content-row {
    max-width: 100%;
    padding-right: 10px;
    padding-left: 10px;
  }
  .listing.min-listing {
    min-height: 0;
  }
  .equal-row {
    flex-direction: row;
  }
  .equal-row > * {
    margin-right: 30px;
  }
  .avatar-row {
    min-width: 84px;
  }
  .avatar-row + .content-row .equal-row > .smaller {
    max-width: 30%;
  }
  .equal-row > .smaller {
    max-width: calc(30% - 19px);
  }
}

@media all and (min-width: 1000px) {
  .content-row {
    max-width: 60%;
  }
  .content-row.full-width {
    max-width: 100%;
  }
  .listing .right-absolute {
    top: 10px;
    padding: 0;
    background: transparent;
    right: 10px;
  }
}

.panel {
  width: 100%;
  padding: 15px;
  background: #f1f1f1;
  border-radius: 3px;
}

.panel.alert {
  background: #fae9e7;
  color: #d84c3d !important;
}

.panel.alert a {
  color: #d84c3d !important;
  text-decoration: underline;
  font-weight: normal;
}

.panel.alert a:hover {
  color: #92291d !important;
}

.panel.success {
  background: #ebf7ec;
  color: #43A047 !important;
}

.panel.success a {
  color: #43A047 !important;
  text-decoration: underline;
  font-weight: normal;
}

.panel.success a:hover {
  color: #255827 !important;
}

.panel.danger {
  background: #ffedd8;
  color: #f08201 !important;
}

.panel.danger a {
  color: #f08201 !important;
  text-decoration: underline;
  font-weight: normal;
}

.panel.danger a:hover {
  color: #8a4b01 !important;
}

.panel.inline-panel {
  display: inline-block;
  width: auto;
}

.panel ul, .panel ol {
  margin: 0;
}

.toggle-content + .panel {
  margin-top: 1.5rem;
}

/**
* Flex-Showcase
*/
.flex-showcase {
  width: 100%;
  overflow: hidden;
}

.flex-showcase ul {
  padding: 0;
  margin: 0;
  width: calc(100% + 50px);
  transform: translate3d(0%, 0, 0);
  display: flex;
  height: 200px;
}

.flex-showcase ul > * {
  flex: 1;
  min-width: 25%;
  background: red;
  display: block;
  padding: 20px;
  color: #fff;
}

.flex-showcase ul > *:first-of-type {
  margin-left: -50px;
}



